.head-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 60px;
}
.head-image{
    width: auto;
    transition: max-width 1s, min-width 1s;
}
.head-text{
    font-family: 'Hachi Maru Pop', cursive;
}
.head-image-expanded{
    min-width: 230px;
    max-width: 250px;
}
.head-image-contracted{
    min-width: 40px;
    max-width: 60px;
}
